import { Detail } from "@/api/shop/index.js"
import vAudit from "./components/audit"
export default {
	data() {
		return {
			info:null,
		};
	},
	components:{
		vAudit
	},
	onLoad(options) {
		const id=options.id;
		
		uni.showLoading();
		Detail(parseInt(id),info=>{
			uni.hideLoading();
			
			this.$nextTick(()=>{
				this.$refs['audit'].SetInfo(info)
			})
		})
	},
	created() {
		
	},
	methods: {
		
	},
}
